.nui-wrapper {
  
  position: fixed;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, -50%);
  margin: 0 auto;

  width: 550px;
  height: 205px;

  user-select: none;

}

.hexBox {

  position: absolute;
  width: fit-content;
  
  img {
    object-fit: contain;
    position: relative;
  }

  &:hover { filter: drop-shadow(0px 0px 8px rgba(86, 253, 253, 1)) }

}

.hexShitInnit { &.activeShadow { filter: drop-shadow(0px 2px 6px rgba(86, 253, 253, 0.5)) } }

.fadeWee-enter {
  opacity: 0;
  transform: scale(0.9);
}
.fadeWee-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 200ms, transform 200ms;
}
.fadeWee-exit {
  opacity: 1;
}
.fadeWee-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 200ms, transform 200ms;
}